*{
	margin: 0;
	padding: 0;
	font-size: 20px;
	scroll-behavior: smooth;
	scroll-padding-top: 80px;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.App{
	display:grid;
	grid-template-rows: auto 1fr auto;
	grid-template-columns: 100%;
	min-height: 100vh;
}
.no-scroll{
	overflow: hidden;
}
.record_compras .product_not_found_compras{
	width: -webkit-max-content;
	width: max-content;
	max-width: 95%;
}
.record_compras .product_not_found_compras h1{
	width: 100%;
	font-size: 30px;
	text-align: center;
}
.containerClearCart{
    font-family: Arial, Helvetica, sans-serif;
    position: fixed;
    inset: 0 0 0 0;
    z-index: 1001;
    width: 100%;
    height: 120%;
    background-color: rgba(0, 0, 0, .8);
}
.ClearCart{
    position: absolute;
    inset: 30% 0 0 0;
    margin: 0 auto;
    background-color: rgb(66, 65, 77);
    color: #fff;
    width: 100%;
    max-width: 450px;
    height: -webkit-max-content;
    height: max-content;
    min-height: 150px;
    border-radius: 10px;
    padding: 20px 0;
    z-index: 100;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.ClearCart h6{
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    padding: 0;
    width: 85%;
}
.ClearCart .Hicon-close{
    position: absolute;
    right: 10px;
}
.HButtonsClearCart{
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: space-evenly;
}
.HButtonsClearCart button{
    cursor: pointer;
    padding: 6px 0;
    width: 100px;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-size: 18px;
    background-color: rgb(92, 91, 102);
}
.HButtonsClearCart .button_sky_blue{
    background-color: rgb(128, 235, 255);
    color: #000;
}
:root{
    --fondo: #ffffff;
}
.Lcontainer{
    flex-grow: 1;
    min-height: 80vh;
    width: 100%;
    background-color: var(--fondo);
    place-content: center;
    display: grid;
    overflow: hidden;
}
.loader {
    font-size: 10px;
    text-indent: -9999em;
    width: 11em;
    height: 11em;
    border-radius: 50%;
    background: linear-gradient(to right, #000 10%, rgba(255, 255, 255, 0) 42%);
    position: relative;
    animation: load3 1.4s infinite linear;
}
.loader:before {
    width: 50%;
    height: 50%;
    background: #000;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
}
.loader:after {
    background-color: var(--fondo);
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    inset: 0 0 0 0;
}
@keyframes load3 {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
