*{
	margin: 0;
	padding: 0;
	font-size: 20px;
	scroll-behavior: smooth;
	scroll-padding-top: 80px;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.App{
	display:grid;
	grid-template-rows: auto 1fr auto;
	grid-template-columns: 100%;
	min-height: 100vh;
}
.no-scroll{
	overflow: hidden;
}
.record_compras .product_not_found_compras{
	width: max-content;
	max-width: 95%;
}
.record_compras .product_not_found_compras h1{
	width: 100%;
	font-size: 30px;
	text-align: center;
}